.vote_buttons_header {
  /* width: 100%; */
  /* background: rgba(255, 255, 255, 1); */
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  background: rgba(0, 0, 51, 1);
  border: 0px;
  border-radius: 5px 5px 0px 0px;
  color: rgba(255, 255, 255, 1);
  margin: 0px -1px 0px -1px;
  padding: 15px 15px;
  /* height: 35px; */
}

.vote_stats_header {
  /* width: 100%; */
  /* background: rgba(255, 255, 255, 1); */
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  background: rgba(0, 0, 51, 1);
  border: 0px;
  border-radius: 5px 5px 0px 0px;
  color: rgba(255, 255, 255, 1);
  margin: 0px -1px 0px -1px;
  padding: 5px 15px;
  /* height: 35px; */
}

.vote_header_wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  width: 80%;
}

.vote_header_left {
  align-self: stretch;
  display: flex;
  flex-grow: 2;
}

.vote_header_left h1 {
  align-self: center;
  display: flex;
  font: 300 15pt "PoppinsLight";
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0em;
  margin-inline-end: 0em;
}

.vote_header_right {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center;
  align-content: center;
  flex-shrink: 1;
}

.vote_header_right h2 {
  align-self: center;
  /* display: flex; */
  /* padding-right: 25px; */
  font: 300 9pt "LatoItalic";
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0em;
  margin-inline-end: 0em;
  text-align: center;
}